import React, { useEffect } from 'react'
import Typed from 'react-typed';
import ReactGA from 'react-ga'

export default function Love() {
    const trackingId = "UA-174225320-1"; // Replace with your Google Analytics tracking ID
    useEffect(() => {
        console.log('Tracking Love web')
        ReactGA.initialize(trackingId);
        ReactGA.set({ page: window.location.pathname })
        ReactGA.pageview(window.location.pathname);
    }, []);
    return (
        <div id='about' className="about">
            <h1 className='welcome' style={{ fontFamily: "'Dancing Script', cursive" }}>Nice to see you</h1>
            <h1 className='type' >
                <Typed
                    strings={[' Hi, I am Ming Chen!  I love Gyubeen Park really much']}
                    typeSpeed={60}
                />
            </h1>
        </div >
    );

}

